import React from "react";




function Navbar() {

  return (
    <>
      <div className="header">
        <a href="#default" className="logo"><span style={{ color: 'white' }}>Being</span> &nbsp;
          <img src="data:image/svg+xml;base64," alt="" height="20" />
          &nbsp; React</a>
        <div className="header-right">
          <h5 className="active" style={{ color: 'white' }}>Made with <span className="heart">&#x2665;</span> by Alok Jadhav</h5>
        </div>
      </div>
    </>
  );
}
export default Navbar;